import './sass/main'; //eslint-disable-line

import Lightbox from '../src/Lightbox';
import LightboxItem from '../src/LightboxItem';

class LightboxItemTest extends LightboxItem {
    constructor(lightbox, key) {
        super(lightbox, key);
        this.content = 'test';
    }

    load() {
        const node = document.createElement('div');
        node.style.display = 'flex';
        node.style.fontFamily = 'Arial';
        node.style.alignItems = 'center';
        node.style.justifyContent = 'center';
        node.style.color = '#000';
        node.style.width = '500px';
        node.style.height = '500px';
        node.style.backgroundColor = '#dadada';
        node.textContent = this.content;
        return node;
    }
}

LightboxItemTest.TYPE_NAME = 'test';

// Lightbox gallery test

window.lb = new Lightbox({
    uid: 1,
    closeOnBlur: true,
    closeOnEscape: true,
    arrowKeyNavigation: true,
    ui: {
        close: true,
        controls: true,
        pagination: true,
    },
});

window.lb.init();

window.lb.on('open', () => {
    console.info('OPEN');
});

window.lb.on('close', () => {
    console.info('CLOSE');
});

window.lb.on('change.before', (prevElement, nextElement) => {
    console.info(`BEFORE CHANGE ${prevElement && prevElement.key} => ${nextElement.key}`);
});

window.lb.on('change.after', (currentElement) => {
    console.info(`AFTER CHANGE ${currentElement.key}`);
});

// Youtube test

new Lightbox({
    uid: 2,
    closeOnBlur: true,
    closeOnEscape: true,
    arrowKeyNavigation: false,
    ui: {
        close: false,
        controls: false,
        pagination: false,
    },
}).init([LightboxItemTest]);
